'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { toggleStoryLike, toggleCommentLike, isStoryLiked, isCommentLiked } from '@/lib/likeService';

interface LikeButtonProps {
    targetId: string;
    targetType: 'story' | 'comment';
    initialLikeCount: number;
    size?: 'small' | 'medium' | 'large';
}

export default function LikeButton({ targetId, targetType, initialLikeCount, size = 'medium' }: LikeButtonProps) {
    const { user } = useAuth();
    const [isLiked, setIsLiked] = useState(false);
    const [likeCount, setLikeCount] = useState(initialLikeCount);
    const [isLoading, setIsLoading] = useState(false);
    const [isChecking, setIsChecking] = useState(true);

    // Kullanƒ±cƒ±nƒ±n beƒüenip beƒüenmediƒüini kontrol et
    useEffect(() => {
        if (!user) {
            setIsChecking(false);
            return;
        }

        async function checkLikeStatus() {
            try {
                const liked = targetType === 'story'
                    ? await isStoryLiked(user.uid, targetId)
                    : await isCommentLiked(user.uid, targetId);
                setIsLiked(liked);
            } catch (error) {
                console.error('Error checking like status:', error);
            } finally {
                setIsChecking(false);
            }
        }

        checkLikeStatus();
    }, [user, targetId, targetType]);

    const handleLike = async () => {
        if (!user || isLoading) return;

        setIsLoading(true);

        // Optimistic UI update
        const newIsLiked = !isLiked;
        const newLikeCount = newIsLiked ? likeCount + 1 : likeCount - 1;
        setIsLiked(newIsLiked);
        setLikeCount(newLikeCount);

        try {
            const result = targetType === 'story'
                ? await toggleStoryLike(user.uid, targetId)
                : await toggleCommentLike(user.uid, targetId);

            // Sunucudan gelen sonu√ßla g√ºncelle
            setIsLiked(result);
        } catch (error) {
            console.error('Error toggling like:', error);
            // Hata durumunda geri al
            setIsLiked(!newIsLiked);
            setLikeCount(likeCount);
        } finally {
            setIsLoading(false);
        }
    };

    if (isChecking) {
        return null; // veya skeleton loader
    }

    const sizeClasses = {
        small: 'like-button-small',
        medium: 'like-button-medium',
        large: 'like-button-large'
    };

    return (
        <button
            className={`like-button ${sizeClasses[size]} ${isLiked ? 'liked' : ''} ${!user ? 'disabled' : ''}`}
            onClick={handleLike}
            disabled={!user || isLoading}
            title={user ? (isLiked ? 'Beƒüeniyi kaldƒ±r' : 'Beƒüen') : 'Beƒüenmek i√ßin giri≈ü yapƒ±n'}
        >
            <span className="like-icon">
                {isLiked ? '‚ù§Ô∏è' : 'ü§ç'}
            </span>
            <span className="like-count">{likeCount}</span>
        </button>
    );
}
